table {
    width: 100%;
    min-height: 687px;
    height: 100%;
}

.list-buttons {
    list-style: none;
    margin-left: 20px;
    margin-right: 20px;
    cursor: pointer;
    font-style: italic;
    font-weight: 700;
    font-size: 18px;
    border: none;
    background: none;
}

.list-buttons:hover {
    opacity: 0.75;
}

td {
    border: 1px solid #E3E6EC;
    text-align: center;
    font-weight: 500;
    font-size: 13px;
    padding: 15px;
    color: #474955;
}